            <Lop :list='list' @gdxqid='gdxqids' />
          </div>
          <div class="box5">
            <Lop1 :gdxq='gdxq' @gqurll='gqurls' />
            <audio id="mu" :src='gdxq.gqur' controls autoplay loop></audio>
            <div class="gcbox1">
              <p class="back1"></p>
              <ul class="gcul1">
                <li v-for="i in lrrc" :key="i.t" class="gcli1"><span class="gcsp1">{{Number(i.t).toFixed(2)}}</span>{{i.c}}</li>             
              </ul>
