<script>
import axiosr from './api/http_axios.js'
import Lop from './components/Lop'
import Lop1 from './components/Lop1'
import Lop2 from './components/Lop2'
let gctime=null
export default {
  name: 'App',
  components:{
    Lop,
    Lop1,
    Lop2,
  },
  data(){
    return{
      uil:[
        {id:1,nar:'发现音乐'},
        {id:2,nar:'我的音乐'},
        {id:3,nar:'朋友'},
        {id:4,nar:'商城'},
        {id:5,nar:'音乐人'},
        {id:6,nar:'下载客户端'},
      ],
      uil1:[
        {id:1,nar:'推荐'},
        {id:2,nar:'排行榜'},
        {id:3,nar:'歌单'},
        {id:4,nar:'主播电台'},
        {id:5,nar:'歌手'},
        {id:6,nar:'新碟上架'},
      ],
      as:'2',
      list:'',
      gdid:'',
      songurl:'',
      lyrc:'',
      lrrc:'',
      gdxq:{
        imr:'',
        gdname:'',
        gdbfl:'',
        gdnum:'',
        gdlb:'',
        cjtime:'',
        gqur:'',
      }
      

    }
  },
  methods:{
    gdxqids(ids){
      this.gdid=ids
      let date = new Date();
      axiosr({
        method:'post',
        xhrFields: { withCredentials: true },
        url:'http://101.201.49.201:3000/playlist/detail?'+date.getTime(),
        data:{
            id:this.gdid
        }
      })
      .then((res)=>{
        // console.log(res)
        this.gdxq.imr=res.data.playlist.coverImgUrl
        this.gdxq.gdnum=res.data.playlist.trackCount
        this.gdxq.gdbfl=res.data.playlist.playCount
        this.gdxq.gdlb=res.data.playlist.tracks
        this.gdxq.gdname=res.data.playlist.name
        let datt=new Date(Number(res.data.playlist.trackNumberUpdateTime))
        let year=datt.getFullYear()
        let mon=datt.getMonth()+1
        let day=datt.getDate()
        this.gdxq.cjtime=`${year}-${mon}-${day}`
      })
    },
    gqurls(ids){
      let date = new Date();
      this.gdxq.gqur=`https://music.163.com/song/media/outer/url?id=${ids}.mp3`
      axiosr({
          method:'post',
          xhrFields: { withCredentials: true },
          url:'http://101.201.49.201:3000/lyric?'+date.getTime(),
          data:{
              id :ids,
          }
        })
        .then((res)=>{
          this.lyrc=res.data.lrc.lyric
          let lrc=this.lyrc
          let oLRC = {
              ti: "", //歌曲名
              ar: "", //演唱者
              al: "", //专辑名
              by: "", //歌词制作人
              offset: 0, //时间补偿值，单位毫秒，用于调整歌词整体位置
              ms: [] //歌词数组{t:时间,c:歌词}
          };
          function createLrcObj(lrc) {
              if(lrc.length==0) return;
              let lrcs = lrc.split('\n');//用回车拆分成数组
              for(let i in lrcs) {//遍历歌词数组
                  lrcs[i] = lrcs[i].replace(/(^\s*)|(\s*$)/g, ""); //去除前后空格
                  let t = lrcs[i].substring(lrcs[i].indexOf("[") + 1, lrcs[i].indexOf("]"));//取[]间的内容
                  let s = t.split(":");//分离:前后文字
                  if(isNaN(parseInt(s[0]))) { //不是数值
                      for (let i in oLRC) {
                          if (i != "ms" && i == s[0].toLowerCase()) {
                              oLRC[i] = s[1];
                          }
                      }
                  }else { //是数值
                      let arr = lrcs[i].match(/\[(\d+:.+?)\]/g);//提取时间字段，可能有多个
                      let start = 0;
                      for(let k in arr){
                          start += arr[k].length; //计算歌词位置
                      }
                      let content = lrcs[i].substring(start);//获取歌词内容
                      for (let k in arr){
                          let t = arr[k].substring(1, arr[k].length-1);//取[]间的内容
                          let s = t.split(":");//分离:前后文字
                          oLRC.ms.push({//对象{t:时间,c:歌词}加入ms数组
                              t: (parseFloat(s[0])*60+parseFloat(s[1])).toFixed(3),
                              c: content
                          });
                      }
                  }
              }
              oLRC.ms.sort(function (a, b) {//按时间顺序排序
                  return a.t-b.t;
              });
          }
          createLrcObj(lrc);
          this.lrrc=oLRC.ms
          gc()
        })
    }
  },
  mounted(){
      let date = new Date();
      axiosr({
        method:'post',
        xhrFields: { withCredentials: true },
        url:'http://101.201.49.201:3000/toplist?'+date.getTime(),
        data:{
            
        }
      })
      .then((res)=>{
        this.list=res.data.list
        this.gdxq.imr=res.data.list[0].coverImgUrl
        this.gdxq.gdname=res.data.list[0].name
        this.gdid=res.data.list[0].id
        axiosr({
          method:'post',
          xhrFields: { withCredentials: true },
          url:'http://101.201.49.201:3000/playlist/detail?'+date.getTime(),
          data:{
              id:this.gdid
          }
        })
        .then((res)=>{
          // console.log(res)
          this.gdxq.gdnum=res.data.playlist.trackCount
          this.gdxq.gdbfl=res.data.playlist.playCount
          this.gdxq.gdlb=res.data.playlist.tracks
          let datt=new Date(Number(res.data.playlist.trackNumberUpdateTime))
          let year=datt.getFullYear()
          let mon=datt.getMonth()+1
          let day=datt.getDate()
          this.gdxq.cjtime=`${year}-${mon}-${day}`
        })
      })
        
  }
}
function gc(){
  clearInterval(gctime)
  gctime=setInterval(()=>{
      let gcsp=document.querySelectorAll('.gcsp')
      let gcul=document.querySelectorAll('.gcul')
      let gcul1=document.querySelectorAll('.gcul1')
      let gcli1=document.querySelectorAll('.gcli1')
      let mu=document.getElementById('mu')
      let ti=mu.currentTime.toFixed(2)
      for(let i=0;i<gcsp.length;i++){
          if(ti==Number(gcsp[i].innerHTML)){
              gcul[0].style.top=`${-i*100}px`
          }
          if(ti==Number(gcsp[i].innerHTML)){
              gcul1[0].style.top=`${200-i*40}px`
          }
          gcli1[i].onclick=function(){
            mu.currentTime=Number(gcsp[i].innerHTML)
          }
      }
  },9) 
}

